@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap);
*{box-sizing:border-box}html{font-family:"Poppins", sans-serif}.app{margin:0;padding:0 30px}.app__nav{display:flex;flex-direction:row}.app__nav--item{padding:10px;border-bottom:2px solid transparent;color:#76798b;text-decoration:none;transition:0.3s}.app__nav--item:hover{border-color:#76798b}.active{border-bottom:2px #3f51b5 solid;color:#3f51b5;font-weight:bold}.active:hover{border-color:#3f51b5}

.lds-ring{display:inline-block;position:relative;width:80px;height:80px}.lds-ring div{box-sizing:border-box;display:block;position:absolute;width:64px;height:64px;margin:8px;border:8px solid #fff;border-radius:50%;-webkit-animation:lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;animation:lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;border-color:black transparent}.lds-ring div :nth-child(1){-webkit-animation-delay:-0.45s;animation-delay:-0.45s}.lds-ring div :nth-child(2){-webkit-animation-delay:-0.3s;animation-delay:-0.3s}.lds-ring div :nth-child(3){-webkit-animation-delay:-0.15s;animation-delay:-0.15s}@-webkit-keyframes lds-ring{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes lds-ring{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.person-row{margin-bottom:10px;border-bottom:1px solid grey}.person-row td{padding:5px 15px}

.people-table{border-collapse:collapse}.people-table__head{border-spacing:10px;background-color:grey}.people-table__head th{padding:5px 10px}

